<template>
    <div>
        <button @click='doSend'>show details</button>
    </div>
</template>

<script>
import http from '../../http-common.js'
import router from '../../router/index.js'

export default {
    name: 'demo',
    props: ['propsId'],
    data (){
        return{
            childId: this.propsId
        }
    },
    methods: {
        doSend () {
            var id = this.childId
            this.$emit('message', id)
            this.$router.replace('/detail')
        }
    },
}
</script>


